@import "../../scss/setup/functions";
@import "../../scss/setup/typography";
@import "../../scss/setup/mixins";
@import "../../scss/setup/variables";

.track-modal {
  width: 550px;
  height: 415px;
  background-color: white;
  padding: 20px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  margin-bottom: unset !important;
  position: relative;
  border-radius: 5px;
  @include for-mobile-layout {
    width: 340px;
    height: 280px;
    padding: 15px;
  }

  iframe {
    max-width: 540px !important;
    max-height: 300px !important;
    border-radius: 5px;
    @include for-mobile-layout {
      width: 310px !important;
      height: 260px !important;
      margin-top: 8px;
    }
  }

  .track-content {
    margin: 20px 0;
    height: fit-content;
    align-items: center;
    @include for-mobile-layout {
      display: none;
    }
    .track-title {
      font-family: "interstate-bold";
      letter-spacing: 0.5px;
      font-size: 18px;
      margin-bottom: 8px;
      max-width: 300px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .track-desc {
      font-size: 15px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3; /* number of lines to show */
      -webkit-box-orient: vertical;
      letter-spacing: 0.4px;
    }
    .track-img {
      margin-left: 20px;
      width: 80px;
      border-radius: 5px;
    }
  }

  .cls-btn {
    align-self: flex-end;
    background-color: transparent;
    font-weight: bold;
    border: none;
    outline: none;
    position: absolute;
    top: 10px;
    right: 5px;
    @include for-mobile-layout {
      top: 5px;
      right: 2px;
    }
    &:hover {
      color: #f30;
    }
    .fa-times {
      font-size: 17px;
      @include for-mobile-layout {
        font-size: 14px;
      }
    }
  }
}
